@use "../../../../../styles/variables";

.imageContainer {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-size: cover;
  background-position: center;
  border: variables.$fg-color 2px solid;
  z-index: 1;
  border-radius: 2em;
}